@import url('https://fonts.googleapis.com/css?family=Squada+One&display=swap');


body {
  background-color: #DDD1CE;

  font-family: 'Squada One', Arial, cursive;
}

* {
  box-sizing: border-box;
}

.App {
  width: 800px;
  margin: 50px auto;

  background-color: rgba(247, 245, 243, 0.5);
  border: 20px solid #E9655D;
  box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.25);
  padding: 0 10px 10px;
}

.description {
  font-size: 12px;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
  display: block;
}


.App .title {
  font-size: 50px;
  color: white;
  background-color: #D45851;
  padding: 30px;
}
