    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

#game {
    background-color: #E9655D;
    margin-top: 10px;
    height: 500px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    justify-items: center;
}

#game .number {
    display: block;
    font-size: 40px;
    background-color: white;
    padding: 15px;
    margin: 10px;
    cursor: pointer;
    position: relative;
    -webkit-transition-duration: 0.25s;
            transition-duration: 0.25s;
    -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
}

#game .number.selected {
    color: #D45851;
}

#game .number.prime-number {
    color: aquamarine;
}

#game .number.prime-number.selected {
    background-color: aquamarine;
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    color: rebeccapurple;
}

#game .number:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 0.8;
}

#game .number:before {
    position: absolute;
    width: 0;
    border-top: 20px solid #E9655D;
    border-right: 20px solid #fff;
    overflow: hidden;
    content: ' ';
    top: 0;
    left: 0;
}


#progress {
    background-color: #E9655D;
    margin-top: 10px;
    height: 80px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #fff;
    justify-items: center;
    align-items: center;
}

#progress .timer {
    font-size: 40px;
    padding: 20px 20px;
}

#progress .button {
    font-size: 20px;
    padding: 10px 20px;
    background-color: #DDD1CE;
    cursor: pointer;
    -webkit-transition-duration: 0.25s;
            transition-duration: 0.25s;
    -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out;
}

#progress .button:hover {
    -webkit-transform: translateY(-10px) scale(1.2);
            transform: translateY(-10px) scale(1.2);
    opacity: 0.8;
    color: aquamarine;
}
