#progress {
    background-color: #E9655D;
    margin-top: 10px;
    height: 80px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    color: #fff;
    justify-items: center;
    align-items: center;
}

#progress .timer {
    font-size: 40px;
    padding: 20px 20px;
}

#progress .button {
    font-size: 20px;
    padding: 10px 20px;
    background-color: #DDD1CE;
    cursor: pointer;
    transition-duration: 0.25s;
    transition-timing-function: ease-in-out;
}

#progress .button:hover {
    transform: translateY(-10px) scale(1.2);
    opacity: 0.8;
    color: aquamarine;
}