@import "./animate.css";

#game {
    background-color: #E9655D;
    margin-top: 10px;
    height: 500px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    justify-items: center;
}

#game .number {
    display: block;
    font-size: 40px;
    background-color: white;
    padding: 15px;
    margin: 10px;
    cursor: pointer;
    position: relative;
    transition-duration: 0.25s;
    transition-timing-function: ease-in-out;
}

#game .number.selected {
    color: #D45851;
}

#game .number.prime-number {
    color: aquamarine;
}

#game .number.prime-number.selected {
    background-color: aquamarine;
    transform: scale(1.3);
    color: rebeccapurple;
}

#game .number:hover {
    transform: scale(1.2);
    opacity: 0.8;
}

#game .number:before {
    position: absolute;
    width: 0;
    border-top: 20px solid #E9655D;
    border-right: 20px solid #fff;
    overflow: hidden;
    content: ' ';
    top: 0;
    left: 0;
}

